import React, { useState } from "react";
import { saveAs } from "file-saver";

import "./BoardSummaryPage.scss";
import { getBoardEstimates, SummaryEstimate } from "../api/estimateApi";
import { useInit } from "../jk/hooks/useInit";
import { formatMinutes } from "../utils/timeParsing";
import { exportEstimatesToTsv } from "../utils/export";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { useWorkSessions } from "../hooks/useWorkSessions";
import { useCurrentWork } from "../hooks/useCurrentWork";
import { useElapsedMinutes } from "../hooks/useElapsed";
import { WorkButton } from "../components/WorkButton";
import { WorkSessions } from "../components/WorkSessions";
import { EndWorkSessionButton } from "../components/EndWorkSessionButton";
import { DeployInfo } from "../components/DeployInfo";

export default function BoardSummaryPage() {
    const t = useProvidedTrello();
    const [summary, setSummary] = useState<SummaryEstimate>();
    const [margin, setMargin] = useState(0.2);
    const workSessions = useWorkSessions(t);
    const currentWork = useCurrentWork();
    const elapsedWorkMinutes = useElapsedMinutes(currentWork.value?.startDate);

    const currentSummary: SummaryEstimate = {
        estimateMinutes: summary?.estimateMinutes ?? 0,
        workMinutes: elapsedWorkMinutes + (summary?.workMinutes ?? 0),
        remainingMinutes: - elapsedWorkMinutes + (summary?.remainingMinutes ?? 0),
    };

    const refresh = async () => {
        const fetched = await getBoardEstimates(t);
        setSummary(fetched);
    };

    useInit(() => {
        refresh().then(() => {
            // Refresh the Notes List when Trello signals that a change has happened
            t.render(async () => {
                await refresh();
            });
        });
    });

    // TODO: export generico

    return (
        <div className='summary'>
            {summary === undefined
                ? <p> Loading... </p>
                : <>
                    <div>
                        Estimate: {formatMinutes(currentSummary.estimateMinutes)}
                    </div>
                    <div>
                        Work: {formatMinutes(currentSummary.workMinutes)}
                    </div>
                    <div>
                        Remaining: {formatMinutes(currentSummary.remainingMinutes)}
                    </div>
                    <WorkButton onEstimateChanged={() => workSessions.refetch()} />
                    <WorkSessions />
                    <EndWorkSessionButton />
                    {/* <button onClick={async () => {
                        const sessions = await getWorkSessions(t);
                        sessions.pop();
                        sessions[sessions.length - 1].endDate = undefined;
                        await setWorkSessions(t, sessions);
                    }}>
                        delete last session
                    </button> */}
                    <div className='export-section'>
                        <div>
                            Estimate margin multiplier
                        </div>
                        <div className='export-inputs'>
                            <input
                                value={margin}
                                onChange={e => {
                                    const val = parseFloat(e.target.value);
                                    if (isNaN(val) || val < 0)
                                        return;
                                    setMargin(val);
                                }}
                                placeholder='insert margin (default is 0.2)...'
                            />
                            <button
                                className='mod-primary'
                                onClick={async () => {
                                    const tsv = await exportEstimatesToTsv(t, margin);
                                    console.log(tsv);
                                    saveAs(
                                        new Blob([tsv], { type: "text/plain;charset=utf-8" }),
                                        "estimate.tsv",
                                    );
                                }}
                            >
                                Export estimates
                            </button>
                        </div>
                    </div>
                    <DeployInfo className="deploy-info" />
                </>
            }
        </div>
    );
}
